<template>
  <div v-if="PlayingData.data" class="playing-detail-page"
    :style="{backgroundImage:`url(${PlayingData.data.cover.detail})`}">
    <div class="cover">
      <div class="video-player" @click="VideoClick">
        <video ref="video" :src="PlayingData.data.playUrl" controls></video>
        <div class="video-cover" v-if="!isPlaying">
          <div class="icon">
            <van-icon name="play" />
          </div>
        </div>
        <div class="close" @click.stop="goBack">
          <van-icon name="cross" />
        </div>
      </div>
      <div class="information">
        <div class="title">{{PlayingData.data.title}}</div>
        <div class="author">
          <span class="tag">#{{PlayingData.data.category}}</span> /
          <span class="author-name">{{PlayingData.data.author.name}}</span>
        </div>
        <div class="description">{{PlayingData.data.description}}</div>
      </div>
      <div class="push-content">
        <h3>相关推荐</h3>
        <div class="content">暂无更多内容</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "playingdetailpage",
    data() {
      return {
        PlayingData: {},
        isPlaying: false
      };
    },
    watch: {
      $router() {
        window.console.log('x');
      }
    },

    methods: {
      goBack() {
        this.$router.go(-1);
        this.$store.commit("setShowBottomBar", true);
      },
      VideoClick() {
        this.isPlaying = !this.isPlaying;
        if (this.isPlaying) {
          this.$refs.video.play();
        } else {
          this.$refs.video.pause();
        }
      }
    },
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.PlayingData = vm.$store.state.PlayingPageDate;
      })
    },

  };
</script>

<style lang="less" scoped>
  .playing-detail-page {
    height: 100vh;
    overflow-anchor: auto;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    .cover {
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.8);

      .push-content {
        padding: 0 15px;
        h3 {
          color: #fafafa;
          font-weight: 100;
        }

        .content {
          color: #666;
        }

      }

      .video-player {
        position: relative;

        video {
          width: 100%;
          display: block;
        }

        .video-cover {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.9);

          .icon {
            width: 50px;
            height: 50px;
            color: #fff;
            font-size: 40px;
            text-align: center;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;

            i {
              line-height: 50px;
            }
          }
        }

        .close {
          width: 30px;
          height: 30px;
          font-size: 20px;
          text-align: center;
          position: absolute;
          top: 0;
          left: 0;
          color: #9c9c9c;

          i {
            line-height: 30px;
          }
        }
      }

      .information {
        color: #fff;
        padding: 10px;

        .title {
          font-weight: bold;
        }

        .author,
        .description {
          font-size: 14px;
          color: #999;
          font-weight: bold;
          padding: 5px 0;
        }
      }
    }
  }
</style>